<template lang='pug'>
  table(
    :class="[block(modifiers)]"
  )
    thead(
      :class="block('thead')"
    )
      tr(
        :class="block('tr')"
      )
        th.prev.available(
          @click='prevMonth',
          :class="block('th')"
        )
          span
        th.month(v-if='showDropdowns', :colspan='showWeekNumbers ? 6 : 5')
          .row.mx-1
            select.monthselect.col(v-model='month')
              option(v-for='(m) in months', :key='m.value', :value='m.value') {{m.label}}
            input.yearselect.col(type='number', v-model='year')
        th.month(v-else='', colspan='5') {{monthName}} {{year}}
        th.next.available(@click='nextMonth')
          span
    tbody
      tr(
        :class="block('tr')"
      )
        th.week(v-if='showWeekNumbers') {{locale.weekLabel}}
        th(v-for='weekDay in locale.daysOfWeek', :key='weekDay') {{weekDay}}
      tr(
        v-for='(dateRow, index) in calendar', 
        :key='index'
        :class="block('tr')"
      )
        td.week(v-if='showWeekNumbers && (index%7 || index===0)')
          | {{dateRow[0] | weeknumber}}
        slot(name='date-slot', v-for='(date, idx) in dateRow')
          td(:class='dayClass(date)', @click="$emit('dateClick', date)", @mouseover="$emit('hoverDate', date)", :key='idx')
            | {{date | dateNum}}

</template>

<script>
import { createBlockGenerator, createStateGenerator } from '@aspectus/bem';
import moment from 'moment'
import {localeData, nextMonth, prevMonth, validateDateRange, yearMonth} from "./util";
import { StylingMixin } from '@aspectus/vue-bem-styling';
import {
  sub,
  getDaysInMonth,
  add,
  setHours,
  getMonth,
  getISODay,
} from 'date-fns'

const name = 'vue-calendar'

export default {
  name,
  mixins: [StylingMixin],
  props: {
    blockName: {
      type: String,
      default: name,
    },
    monthDate: Date,
    localeData: Object,
    start: Date,
    end: Date,
    minDate: Date,
    maxDate: Date,
    showDropdowns: {
      type: Boolean,
      default: false,
    },
    showWeekNumbers: {
      type: Boolean,
      default: false,
    },
    dateFormat: {
      type: Function,
      default: null
    }
  },
  data () {
    return {
      currentMonthDate: this.monthDate || this.start || new Date(),
    }
  },
  computed: {
    block() {
      return createBlockGenerator()(this.blockName);
    },
    monthName () {
      return this.locale.monthNames[this.currentMonthDate.getMonth()]
    },
    year: {
      get () {
        return this.currentMonthDate.getFullYear()
      },
      set (value) {
        let newDate = validateDateRange(new Date(value, this.month, 1), this.minDate, this.maxDate)

        this.$emit('change-month', {
          month: newDate.getMonth(),
          year: newDate.getFullYear(),
        });
      }
    },
    month: {
      get () {
        return this.currentMonthDate.getMonth()
      },
      set (value) {
        let newDate = validateDateRange(new Date(this.year, value, 1), this.minDate, this.maxDate)

        this.$emit('change-month', {
          month: newDate.getMonth(),
          year: newDate.getFullYear(),
        });
      }
    },
    calendar () {
      let month = this.month
      let year = this.currentMonthDate.getFullYear()
      let daysInMonth = new Date(year, month, 0).getDate()
      let firstDay = new Date(year, month, 1)
      let lastDay = new Date(year, month, daysInMonth)

      let lastDate = sub(firstDay, { month: 1 } )
      let lastMonth = lastDate.getMonth()
      let lastYear = lastDate.getFullYear()

      let daysInLastMonth = getDaysInMonth(lastDate)
      let dayOfWeek = firstDay.getDay()

      let calendar = []

      for (let i = 0; i < 6; i++) {
        calendar[i] = [];
      }

      let startDay = daysInLastMonth - dayOfWeek + this.locale.firstDay + 1
      if (startDay > daysInLastMonth)
        startDay -= 7

      if (dayOfWeek === this.locale.firstDay)
        startDay = daysInLastMonth - 6;

      let curDate = new Date(lastYear, lastMonth, startDay, 12, 0, 0);

      console.log(curDate);
      for (let i = 0, col = 0, row = 0; i < 6 * 7; i++, col++, curDate = add(curDate, {'day': 1}) ) {
        if (i > 0 && col % 7 === 0) {
          col = 0;
          row++;
        }
        calendar[row][col] = curDate
        curDate = setHours(curDate, 12);
        console.log(curDate);
      }

      return calendar
    },
    months () {
      let monthsData = this.locale.monthNames.map((m, idx) => ({ label: m, value: idx }))

      if (this.maxDate && this.minDate) {
        let y = this.maxDate.getFullYear() - this.minDate.getFullYear();
        if (y < 2) {
          // get months
          let months = [];
          if (y < 1) {
            for (let i = this.minDate.getMonth(); i <= this.maxDate.getMonth(); i++) {
              months.push(i);
            }
          } else {
            for (let i = 0; i <= this.maxDate.getMonth(); i++) {
              months.push(i);
            }
            for (let i = this.minDate.getMonth(); i < 12; i++) {
              months.push(i);
            }
          }
          // do filter
          if (months.length > 0) {
            return monthsData.filter((m) => {
              return months.find(i => m.value === i) > -1;
            });
          }
        }
      }
      return monthsData;
    },
    locale () { return localeData(this.localeData) }
  },
  watch: {
    monthDate (value) {
      this.changeMonthDate(value, false)
    }
  },
  filters: {
    dateNum (value) {
      console.log(value);
      return value.date()
    },
    weeknumber (value) {
      return value.week()
    }
  },
  methods: {
    prevMonth() {
      this.changeMonthDate(prevMonth(this.currentMonthDate))
    },
    nextMonth() {
      this.changeMonthDate(nextMonth(this.currentMonthDate))
    },
    changeMonthDate (date, emit = true) {
      let year_month = yearMonth(this.currentMonthDate)
      this.currentMonthDate = validateDateRange(date, this.minDate, this.maxDate)
      if(emit && year_month !== yearMonth(this.currentMonthDate)) {
        this.$emit('change-month', {
          month: this.currentMonthDate.getMonth(),
          year: this.currentMonthDate.getFullYear(),
        })
      }
    },
    dayClass (date) {
      let dt = new Date(date)
      dt.setHours(0, 0, 0, 0)
      let start = new Date(this.start)
      start.setHours(0, 0, 0, 0)
      let end = new Date(this.end)
      end.setHours(0, 0, 0, 0)

      let classes = {
        off: getMonth(date) !== this.month,
        weekend: getISODay(date) > 5,
        today: dt.setHours(0, 0, 0, 0) == new Date().setHours(0, 0, 0, 0),
        active: dt.setHours(0, 0, 0, 0) == new Date(this.start).setHours(0, 0, 0, 0) || dt.setHours(0, 0, 0, 0) == new Date(this.end).setHours(0, 0, 0, 0),
        // 'in-range': dt >= start && dt <= end,
        'start-date': dt.getTime() === start.getTime(),
        'end-date': dt.getTime() === end.getTime(),
        disabled: (this.minDate && moment(dt).startOf("day").isBefore(moment(this.minDate).startOf("day")))
          || (this.maxDate && moment(dt).startOf("day").isAfter(moment(this.maxDate).startOf("day"))),
      }
      return this.dateFormat ? this.dateFormat(classes, date) : classes

    }
  },
  
}
</script>

<style scoped lang="scss">
  th, td {
      padding: 2px;
      background-color: white;
  }

  td.today {
      font-weight: bold;
  }

  td.disabled {
      pointer-events: none;
      background-color: #eee;
      border-radius: 0;
      opacity: 0.6;
  }

  @function str-replace($string, $search, $replace: "") {
      $index: str-index($string, $search);

      @if $index {
          @return str-slice($string, 1, $index - 1) + $replace + str-replace(str-slice($string, $index + str-length($search)), $search, $replace);
      }

      @return $string;
  }

  $carousel-control-color: #ccc !default;
  $viewbox: '-2 -2 10 10';
  $carousel-control-prev-icon-bg: str-replace(url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='#{$viewbox}'%3E%3Cpath d='M5.25 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3E%3C/svg%3E"), "#", "%23") !default;
  $carousel-control-next-icon-bg: str-replace(url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='#{$viewbox}'%3E%3Cpath d='M2.75 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3E%3C/svg%3E"), "#", "%23") !default;

  .fa {
      display: inline-block;
      width: 100%;
      height: 100%;
      background: transparent no-repeat center center;
      background-size: 100% 100%;
      fill: $carousel-control-color;
  }

  .prev, .next {
      &:hover {
          background-color: transparent !important;
      }

      .fa:hover {
          opacity: 0.6;
      }
  }

  .chevron-left {
      width: 16px;
      height: 16px;
      display: block;
      background-image: $carousel-control-prev-icon-bg;
  }

  .chevron-right {
      width: 16px;
      height: 16px;
      display: block;
      background-image: $carousel-control-next-icon-bg;
  }

  .yearselect {
      padding-right: 1px;
      border: none;
      appearance: menulist;
  }

  .monthselect {
      border: none;
  }
</style>


